@import url('https://cdn.rawgit.com/moonspam/NanumSquare/master/nanumsquare.css');

body{
    font-family: 'NanumSquare';
}

.movies .movie {
    background-color: rgba(255, 255, 255, 0.4);
    margin-bottom: 70px;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color: #fff;
    box-shadow:
        0 13px 27px -5px rgba(50, 50, 93, 0.25),
        0 8px 16px -8px rgba(0, 0, 0, 0.3),
        0 -6px 16px -6px rgba(50, 50, 93, 0.25);
}

.movies .movie a {
    display: grid;
    grid-template-columns: minmax(150px, 1fr)2fr;
    grid-gap: 20px;
    text-decoration: none;
    color: inherit;
}

.movie img {
    position: relative;
    top: -50px;
    max-width: 250px;
    width: 100%;
    margin-right: 30px;
    box-shadow:
        0 30px 60px -12px rgba(50, 50, 93, 0.25),
        0 18px 36px -18px rgba(0, 0, 0, 0.3),
        0 -12px 36px -8px rgba(50, 50, 93, 0.025);
    ;
}


.movie .movie__title,
.movie .movie__year {
    margin: 0;
    font-weight: 800;
    color: #fff;
    margin-bottom: 10px;
}

.movie .movie {
    margin-bottom: 5px;
    font-size: 24px;
    color: #fff;
}

.movie .movie__genres {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    margin: 5px 0;
}

.movie .movie__year {
    margin-right: 10px;
    font-size: 16px;
    color: #fff;
    font-weight: 600;}

.movie__genres li{
    margin-right: 10px;
    font-size: 11px;
    color: #fff;
    font-weight: 600;
    background-color: #101010;
    padding: 5px 8px;
    border-radius: 20px;

}

.movie__summary{
    color: #fff;
    font-weight: 400;
    line-height: 26px;
    font-size: 14px;
}