.nav{
    z-index: 1;
    position: fixed;
    top: 40%;
    right: 10px;
    display: flex;
    flex-direction: column;
    background-color:rgba(255, 255, 255, 0.4);
    padding: 40px 20px;
    box-shadow: 
    0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3),
    0 -6px 16px -6px rgba(50, 50, 93, 0.25);

    border-radius: 5px;
}

.nav a{
    text-decoration: none;
    color: #fff;
    text-transform: uppercase;
    font-size: 16px;
    text-align: center;
    font-weight: 600;
    display: inline-block;
    padding: 20px 0;
}

/* .nav a:not(:last-child) {margin-bottom: 20px;} */

@media screen and (max-width:1090px){
    .nav{
        left:initial;
        top: initial;
        top: 0px;
        width: 100%;
        display: flex;
        flex-direction:row;
        gap: 100px;
        justify-content: center;
        align-items: center;
        padding: 20px 0;
        background-color: #eaeaea;
        
    }

    .nav a{
        padding: 0;
        margin: 0;
        display: inline-block;
        font-size: 26px;
        color: #101010;
        font-weight: 800;

    }
}