.container{
    height: 100%;
    display: flex;
    justify-content: center;
}

.loader{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
}

.loader__text{
    color: #fff;
    font-size: 6rem;
}

/* minmax(400px,1fr) 각 셀을 400px밑으로 줄어들지 x, 이상을 경우 그리드 컨테이너 너비에 맞게 */

.movies{
    display: grid;
    grid-template-columns: repeat(2,minmax(400px,1fr));
    grid-gap: 100px;
    padding: 50px;
    width: 80%;
    padding-top: 70px;
}

@media screen and (max-width:1090px) {
    .movies{
        grid-template-columns: 1fr;
        width: 100%;
    }
}

